import React, { useContext, useState } from 'react';
import Modal from "react-bootstrap/Modal";
import {Button, Form} from "react-bootstrap";
import { observer } from 'mobx-react-lite';
import { Context } from '../..';
import { createDepartments } from '../../http/departmentsAPI';

const CreateDepartments = ({show, onHide}) => {

    const { departments } = useContext(Context)
    const [depImg, setDepImg] = useState(null)
    const [depName, setDepName] = useState('')
    const [depText, setDepText] = useState('')

    const selectFile = e => {
        setDepImg(e.target.files[0])
    }

    const addDepartment = () => {
        const formData = new FormData()
        formData.append('image', depImg)
        formData.append('name', depName)
        formData.append('text', depText)
        createDepartments(formData).then(data => onHide())
    }

    return (
        <Modal
            show={show}
            onHide={onHide}
            size="lg"
            centered
        >
        <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
                 
            </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Form>
                <Form.Control
                    className="mt-3"
                    placeholder="  "
                    value={depName}
                    onChange={e => setDepName(e.target.value)}
                />
                <Form.Control
                    className="mt-3"
                    placeholder="  "
                    value={depText}
                    onChange={e => setDepText(e.target.value)}
                />
                <Form.Control
                    className="mt-3"
                    placeholder="  "
                    type="file"
                    onChange={selectFile}
                />
            </Form>
        </Modal.Body>
        <Modal.Footer>
            <Button variant={"outline-danger"} onClick={onHide}></Button>
            <Button variant={"outline-success"} onClick={addDepartment}></Button>
        </Modal.Footer>
        </Modal>
        );
}

export default CreateDepartments