import React, { useContext, useState } from 'react';
import Modal from "react-bootstrap/Modal";
import {Button, Form, Row, Col} from "react-bootstrap";
import { Context } from '../..';
import { createMassMedia } from '../../http/massmediaAPI';

const CreateMassMedia = ({show, onHide}) => {

    const {massmedia} = useContext(Context)
    const [massmImg, setMassmImg] = useState(null)
    const [massmName, setMassmName] = useState('')
    const [massmDate, setMassmDate] = useState('')
    const [massmText, setMassmText] = useState('')
    const [info, setInfo] = useState([])

    const selectFile = e => {
        setMassmImg(e.target.files[0])
    }

    const addInfo = () => {
        setInfo([...info, {title: '', description: '', number: Date.now()}])
    }
    const removeInfo = (number) => {
        setInfo(info.filter(i => i.number !== number))
    }

    const addMassMedia = () => {
        const formData = new FormData()
        formData.append('name', massmName)
        formData.append('text', massmText)
        formData.append('date', massmDate)
        formData.append('image', massmImg)
        createMassMedia(formData).then(data => onHide())
    }

    return (
        <Modal
            show={show}
            onHide={onHide}
            size="lg"
            centered
        >
        <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
                 
            </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Form>
                <Form.Control
                    className="mt-3"
                    placeholder="  "
                    value={massmName}
                    onChange={e => setMassmName(e.target.value)}
                />
                <Form.Control
                    className="mt-3"
                    placeholder="  "
                    value={massmText}
                    onChange={e => setMassmText(e.target.value)}
                />
                <Form.Control
                    className="mt-3"
                    placeholder="    "
                    value={massmDate}
                    onChange={e => setMassmDate(e.target.value)}
                />
                <Form.Control
                    className="mt-3"
                    placeholder=" "
                    type="file"
                    onChange={selectFile}
                />
                <hr/>
                <Button
                    variant={"outline-dark"}
                    onClick={addInfo}
                >
                     
                </Button>
                {
                    info.map(i =>
                        <Row classname="mt-4" key={i.number}>
                            <Col md={4}>
                            <Form.Control
                                className="nt-3"
                                placeholder="  "
                                type="text"
                            />
                            </Col>
                            <Col md={4}>
                            <Form.Control
                                className="nt-3"
                                placeholder="  "
                                type="text"
                            />
                            </Col>
                            <Col md={4}>
                                <Button onClick={() => removeInfo(i.number)} variant={"outline-danger"}></Button>
                            </Col>
                        </Row>
                    )
                }
            </Form>
        </Modal.Body>
        <Modal.Footer>
            <Button variant={"outline-danger"} onClick={onHide}></Button>
            <Button variant={"outline-success"} onClick={addMassMedia}></Button>
        </Modal.Footer>
        </Modal>
        );
}

export default CreateMassMedia