import React, { useState } from 'react';
import Modal from "react-bootstrap/Modal";
import {Button, Form, Row, Col} from "react-bootstrap";

const CreateForeignServices = ({show, onHide}) => {
    const [info, setInfo] = useState([])
    const addInfo = () => {
        setInfo([...info, {title: '', description: '', number: Date.now()}])
    }
    const removeInfo = (number) => {
        setInfo(info.filter(i => i.number !== number))
    }

    return (
        <Modal
            show={show}
            onHide={onHide}
            size="lg"
            centered
        >
        <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
                 
            </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Form>
                <Form.Control
                    className="mt-3"
                    placeholder="  "
                />
                <Form.Group className="mt-3">
                    <Form.Control 
                        as="textarea" 
                        rows={5} 
                        placeholder="  "/>
                    </Form.Group>
                <Form.Control
                    className="mt-3"
                    placeholder=" "
                    type="file"
                />
                <hr/>
                <Button
                    variant={"outline-dark"}
                    onClick={addInfo}
                >
                     
                </Button>
                {
                    info.map(i =>
                        <Row classname="mt-4" key={i.number}>
                            <Col md={4}>
                            <Form.Control
                                className="nt-3"
                                placeholder="  "
                                type="text"
                            />
                            </Col>
                            <Col md={4}>
                            <Form.Control
                                className="nt-3"
                                placeholder="  "
                                type="text"
                            />
                            </Col>
                            <Col md={4}>
                                <Button onClick={() => removeInfo(i.number)} variant={"outline-danger"}></Button>
                            </Col>
                        </Row>
                    )
                }
            </Form>
        </Modal.Body>
        <Modal.Footer>
            <Button variant={"outline-danger"} onClick={onHide}></Button>
            <Button variant={"outline-success"} onClick={onHide}></Button>
        </Modal.Footer>
        </Modal>
        );
}

export default CreateForeignServices