import React, { useContext, useState } from 'react';
import Modal from "react-bootstrap/Modal";
import {Button, Form} from "react-bootstrap";
import { Context } from '../..';
import { createServices } from '../../http/servicesAPI';

const CreateServices = ({show, onHide}) => {
    const {popularSev} = useContext(Context)

    const selectFile = e => {
        setServiceImg(e.target.files[0])
    }

    const [serviceImg, setServiceImg] = useState(null)
    const [serviceName, setServiceName] = useState('')
    const [serviceDescription, setServiceDescription] = useState('')

    const addServices = () => {
        const formData = new FormData()
        formData.append('name', serviceName)
        formData.append('text', serviceDescription)
        formData.append('image', serviceImg)
        createServices(formData).then(data => onHide())
    }

    return (
        <Modal
            show={show}
            onHide={onHide}
            size="lg"
            centered
        >
        <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
                 
            </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Form>
                <Form.Control
                    className="mt-3"
                    value={serviceName}
                    onChange={e => setServiceName(e.target.value)}
                    placeholder="  "
                />
                <Form.Control
                    className="mt-3"
                    value={serviceDescription}
                    onChange={e => setServiceDescription(e.target.value)}
                    placeholder="  "
                />
                <Form.Control
                    className="mt-3"
                    onChange={selectFile}
                    placeholder=" "
                    type="file"
                />
            </Form>
        </Modal.Body>
        <Modal.Footer>
            <Button variant={"outline-danger"} onClick={onHide}></Button>
            <Button variant={"outline-success"} onClick={addServices}></Button>
        </Modal.Footer>
        </Modal>
        );
}

export default CreateServices